<template>
  <div class="index_con" :class="theme ? 'theme-background-bg' : ''">
    <div class="index_banner_box">
      <carousel :imgList="banner" @bannerdetail="bannerdetail"></carousel>
      <!-- <el-carousel :interval="10000" arrow="never" height="540px">
        <el-carousel-item
          @click="bannerdetail(item.chainedAddress)"
          v-for="item in banner"
          :key="item.bannerId"
        >
          <el-image
            @click="bannerdetail(item.chainedAddress)"
            :style="
              item.chainedAddress
                ? 'cursor: pointer;height: 540px; width: 100%;'
                : 'height: 540px; width: 100%;'
            "
            :src="item.imagePath"
          ></el-image>
        </el-carousel-item>
      </el-carousel> -->
    </div>
    <div class="index_con_box1">
      <div class="index_con_box1c">
        <div class="index_title1 m_b50">
          <p>精选资源</p>
        </div>
        <div class="index_con_box1l">
          <div class="index_title2">
            <img src="@/assets/icon/index_ico1.png" />
            <span>新书速递</span>
          </div>
          <div class="index_con_box1lb">
            <div class="index_con_box1lb1" v-if="goods[0]">
              <el-image
                class="index_con_box1lbimg"
                @click="tobookDetail(goods[0].goodsId, goods[0].goodsType)"
                :src="goods[0].cover"
              >
                <div slot="error" class="image-slot">
                  <img
                    class="index_con_box1lbimg"
                    src="@/assets/images/default_img.jpg"
                  />
                </div>
              </el-image>
              <div class="index_con_box1lbcon">
                <p class="ellipsis-aRow">
                  <span class="book-label" v-if="goods[0].sourceFile">{{
                    goods[0].sourceFile
                  }}</span>
                  <span
                    @click="tobookDetail(goods[0].goodsId, goods[0].goodsType)"
                    class="title"
                    style="cursor: pointer"
                    >{{ goods[0].goodsTitle }}</span
                  >
                </p>
                <font v-if="goods[0].goodsType != 'paper'" class="ellipsis-aRow"
                  >作者：{{
                    goods[0].author == null || goods[0].author == ""
                      ? "-"
                      : goods[0].author
                  }}
                </font>
                <font class="ellipsis-aRow"
                  >提供机构：{{
                    goods[0].instName == null || goods[0].instName == ""
                      ? "-"
                      : goods[0].instName
                  }}</font
                >
                <div class="index_con_box1lbcon_tag">
                  <span
                    class="swtag"
                    v-for="(item, index) in goods[0].keyword"
                    :key="index"
                  >
                    {{ item }}
                  </span>
                </div>
                <div class="index_con_box1lbcon_je">
                  {{ goods[0].realPrice | tofixed2("￥") }}
                  <span :class="goods[0].prices == 0 ? 'yprice2' : 'yprice'">{{
                    goods[0].prices | tofixed2("￥")
                  }}</span>
                </div>
                <div class="index_con_box1lbcon_jy">编辑推荐语：</div>
                <div class="index_con_box1lbcon_nr ellipsis-twoRow">
                  {{
                    goods[0].authorRecommendation == null ||
                    goods[0].authorRecommendation == ""
                      ? "-"
                      : goods[0].authorRecommendation
                  }}
                </div>
              </div>
            </div>
            <div class="index_con_box1lb2">
              <div
                class="index_con_box1lb2_li"
                v-for="(item, index) in goods"
                :key="index"
                v-show="index !== 0 && index !== 4"
              >
                <el-image class="index_con_box1lb2_lil" :src="item.cover">
                  <div slot="error" class="image-slot">
                    <img
                      class="index_con_box1lb2_lil"
                      src="@/assets/images/default_img.jpg"
                    />
                  </div>
                </el-image>
                <div class="index_con_box1lb2_lir">
                  <p
                    class="ellipsis-aRow"
                    style="cursor: pointer"
                    @click="tobookDetail(item.goodsId, item.goodsType)"
                  >
                    {{ item.goodsTitle }}
                  </p>
                  <el-tooltip
                    :content="item.author"
                    placement="top"
                    v-ak-tooltip-auto-show
                  >
                    <span v-if="item.goodsType != 'paper'" class="ellipsis-aRow"
                      >作者：
                      {{
                        item.author == null || item.author == ""
                          ? "-"
                          : item.author
                      }}</span
                    >
                  </el-tooltip>
                  <el-tooltip
                    :content="item.instName"
                    placement="top"
                    v-ak-tooltip-auto-show
                    ><span class="ellipsis-aRow"
                      >提供机构：{{
                        item.instName == null || item.instName == ""
                          ? "-"
                          : item.instName
                      }}</span
                    >
                  </el-tooltip>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="index_con_box1r">
          <div class="index_title2">
            <img src="@/assets/icon/index_ico2.png" />
            <span>好书排行</span>
          </div>
          <div class="index_con_box1r_con">
            <li v-for="(item, index) in goodBook" :key="index">
              <div :class="'index_con_box1r_con_num icbc_num' + ++index">
                <span>{{ index }}</span>
              </div>
              <div class="index_con_box1r_con_r">
                <div class="icbcr_box_first" v-show="index === 1">
                  <el-image
                    @click="tobookDetail(item.bookId, item.goodsType)"
                    class="icbcr_box_firstl"
                    style="cursor: pointer"
                    :src="item.cover"
                    fit="contain"
                  ></el-image>
                  <div class="icbcr_box_firstr">
                    <p
                      @click="tobookDetail(item.bookId, item.goodsType)"
                      style="cursor: pointer"
                      class="ellipsis-aRow"
                    >
                      {{ item.bookName }}
                    </p>
                    <span>
                      {{ item.salesVolume }}
                      <img src="@/assets/icon/index_ico_huo.png" />
                    </span>
                    <font class="ellipsis-aRow">{{ item.deptName }}</font>
                    <font class="ellipsis-twoRow">{{
                      item.authorRecommendation
                    }}</font>
                  </div>
                </div>
                <div class="icbcr_box" v-show="index !== 1">
                  <p
                    style="cursor: pointer"
                    @click="tobookDetail(item.bookId, item.goodsType)"
                    class="ellipsis-aRow"
                  >
                    {{ item.bookName }}
                  </p>
                  <span :style="index >= 4 ? 'color: #F6A200' : ''">
                    <span>{{ item.salesVolume }}</span>
                    <img
                      v-if="index < 4"
                      src="@/assets/icon/index_ico_huo.png"
                    />
                    <img
                      v-if="index >= 4"
                      src="@/assets/icon/index_ico_huo2.png"
                    />
                  </span>
                </div>
              </div>
            </li>
          </div>
        </div>
        <div class="index_con_boxcb">
          <span>猜你<br />喜欢</span>
          <div class="index_con_boxcb_ul">
            <div
              class="index_con_boxcb_li"
              v-for="(item, index) in GuessFavourite"
              :key="index"
            >
              <el-image
                @click="tobookDetail(item.goodsId, item.goodsType)"
                class="index_con_boxcb_liimg"
                :src="item.cover"
                ><div slot="error" class="image-slot">
                  <img
                    class="index_con_boxcb_liimg"
                    src="@/assets/images/default_img.jpg"
                  />
                </div>
              </el-image>
              <p
                @click="tobookDetail(item.goodsId, item.goodsType)"
                class="ellipsis-aRow"
              >
                {{ item.goodsTitle }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="index_con_box2">
      <div class="index_con_box2c">
        <div class="index_title1 m_b50">
          <p>热门图书</p>
          <span class="more" style="cursor: pointer" @click="viewMore()"
            >查看更多<img g src="@/assets/icon/indexico_to.png"
          /></span>
        </div>
        <div class="index_con_box2tabs">
          <div class="index_con_box2tabsl">
            <li
              v-for="(item, index) in goodsTypeList"
              :key="index"
              :class="
                item.value === goodsTypeList[goodsType].value ? 'active' : ''
              "
              @click="(goodsType = index), gethomePageselHotBookList()"
            >
              {{ item.lable }}
            </li>
          </div>
          <div class="index_con_box2tabsr">
            <div class="index_con_box2tabsr1" v-if="homePageselHotBook.length">
              <el-image
                style="cursor: pointer"
                @click="
                  tobookDetail(
                    homePageselHotBook[0].relevanceId,
                    homePageselHotBook[0].goodsType
                  )
                "
                class="index_con_box2tabsr1img"
                :src="homePageselHotBook[0].cover"
                ><div slot="error" class="image-slot">
                  <img
                    class="index_con_box2tabsr1img"
                    src="@/assets/images/default_img.jpg"
                  />
                </div>
              </el-image>
              <div class="index_con_box1lbcon index_con_box2tabsr1b">
                <p style="font-size: 16px !important" class="ellipsis-twoRow">
                  <span
                    class="book-label"
                    v-if="homePageselHotBook[0].sourceFile"
                    >{{ homePageselHotBook[0].sourceFile }}</span
                  >
                  <span
                    @click="
                      tobookDetail(
                        homePageselHotBook[0].relevanceId,
                        homePageselHotBook[0].goodsType
                      )
                    "
                    class="title"
                    style="cursor: pointer"
                    >{{ homePageselHotBook[0].relevanceName }}</span
                  >
                </p>
                <font v-if="homePageselHotBook[0].goodsType != 'paper'"
                  >作者：{{
                    homePageselHotBook[0].authorName == null ||
                    homePageselHotBook[0].authorName == ""
                      ? "-"
                      : homePageselHotBook[0].authorName
                  }}</font
                >
                <font
                  >提供机构：{{
                    homePageselHotBook[0].instName == null ||
                    homePageselHotBook[0].instName == ""
                      ? "-"
                      : homePageselHotBook[0].instName
                  }}</font
                >
                <div class="index_con_box1lbcon_tag">
                  <!-- <el-tag v-for="(item, index) in homePageselHotBook[0].keyWord" :key="index" type="info">{{ item }}
                  </el-tag> -->
                  <span
                    class="swtag"
                    v-for="(item, index) in homePageselHotBook[0].keyWord"
                    :key="index"
                  >
                    {{ item }}
                  </span>
                </div>
                <div class="index_con_box1lbcon_je">
                  {{ homePageselHotBook[0].realPrice | tofixed2("￥") }}
                  <span
                    :class="
                      homePageselHotBook[0].prices == 0 ? 'yprice2' : 'yprice'
                    "
                    >{{ homePageselHotBook[0].prices | tofixed2("￥") }}</span
                  >
                </div>
                <div class="index_con_box1lbcon_jy">编辑推荐语：</div>
                <div class="index_con_box1lbcon_nr ellipsis-twoRow">
                  {{
                    homePageselHotBook[0].authorRecommendation == null ||
                    homePageselHotBook[0].authorRecommendation == ""
                      ? "-"
                      : homePageselHotBook[0].authorRecommendation
                  }}
                </div>
              </div>
            </div>
            <div class="index_con_box2tabsr2">
              <div
                class="index_con_box2tabsr2_li"
                v-show="index !== 0"
                v-for="(item, index) in homePageselHotBook"
                :key="index"
              >
                <el-image
                  style="cursor: pointer"
                  @click="tobookDetail(item.relevanceId, item.goodsType)"
                  class="index_con_box2tabsr2_liimg"
                  :src="item.cover"
                  ><div slot="error" class="image-slot">
                    <img
                      class="index_con_box2tabsr2_liimg"
                      src="@/assets/images/default_img.jpg"
                    />
                  </div>
                </el-image>
                <el-tooltip
                  :content="item.relevanceName"
                  placement="top"
                  v-ak-tooltip-auto-show
                >
                  <p
                    style="cursor: pointer"
                    @click="tobookDetail(item.relevanceId, item.goodsType)"
                    class="ellipsis-twoRow"
                  >
                    {{ item.relevanceName }}
                  </p>
                </el-tooltip>
              </div>
            </div>
            <el-empty
              v-if="homePageselHotBook.length == 0"
              description="暂无数据"
            ></el-empty>
          </div>
        </div>
      </div>
    </div>
    <div class="index_con_box3">
      <div class="index_con_box3c">
        <div class="index_title1 m_b50">
          <p>特色专题</p>
          <span
            class="more"
            style="cursor: pointer"
            @click="$router.push('/special/index')"
            >查看更多<img src="@/assets/icon/indexico_to.png"
          /></span>
        </div>
        <div id="certify">
          <div class="swiper-container">
            <div class="swiper-wrapper">
              <div
                style="cursor: pointer"
                @click="todetail(item.relevanceId)"
                class="swiper-slide"
                v-for="item in selSubject"
                :key="item.id"
              >
                <el-image class="img_box" :src="item.cover">
                  <div slot="error" class="image-slot">
                    <img
                      class="img_box"
                      src="@/assets/images/default_img.jpg"
                    />
                  </div>
                </el-image>
              </div>
            </div>
          </div>
          <!-- <div class="swiper-button-prev"></div>
          <div class="swiper-button-next"></div> -->
        </div>
        <!-- <div class="index_con_box3cs">
          <el-carousel
            :interval="10000"
            type="card"
            height="371px"
            indicator-position="none"
          >
            <el-carousel-item
              v-for="item in selSubject"
              :key="item.id"
              height="371px"
            >
              <span style="width: 700px; height: 371px; display: block">
                <el-image
                  style="
                    width: 691px;
                    height: 363px;
                    background: #f2f2f2;
                    margin-top: 5px;
                    margin-left: 5px;
                    box-shadow: 0 0 4px #ccc;
                    border-radius: 10px;
                    overflow: hidden;
                  "
                  fit="contain"
                  :src="item.cover"
                  @click="todetail(item.relevanceId)"
                ></el-image>
              </span>
            </el-carousel-item>
          </el-carousel>
        </div> -->
      </div>
    </div>
    <div class="index_con_box4">
      <div class="index_con_box4c">
        <div class="index_title1 m_b45">
          <p>优秀作者</p>
          <span
            class="more"
            style="cursor: pointer"
            @click="$router.push('/authorLibrary/index')"
            >查看更多<img src="@/assets/icon/indexico_to.png"
          /></span>
        </div>
        <div class="index_con_box4cs">
          <div
            class="index_con_box4cs_li"
            v-for="(item, index) in selAuthor"
            :key="index"
          >
            <div class="index_con_box4cs_lic">
              <el-image
                class="index_con_box4cs_lic"
                :src="item.cover"
                @click="toauthorDetail(item.relevanceId)"
              >
                <div slot="error" class="image-slot">
                  <img class="index_con_box4cs_lic" :src="defaultImg" />
                </div>
              </el-image>
              <el-tooltip
                :content="item.relevanceName"
                placement="top"
                v-ak-tooltip-auto-show
              >
                <p
                  class="ellipsis-aRow"
                  style="cursor: pointer"
                  @click="toauthorDetail(item.relevanceId)"
                >
                  {{ item.relevanceName }}
                </p>
              </el-tooltip>

              <el-tooltip
                :content="item.academicSector"
                placement="top"
                v-ak-tooltip-auto-show
              >
                <span class="ellipsis-aRow"
                  >学术领域：{{ item.academicSector }}</span
                >
              </el-tooltip>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="index_con_box5">
      <div class="index_con_box5c">
        <div class="index_title1 m_b50">
          <p>合作机构</p>
          <span
            class="more"
            style="cursor: pointer"
            @click="$router.push('/coagency/index')"
            >查看更多<img src="@/assets/icon/indexico_to.png"
          /></span>
        </div>
        <div class="index_con_box5cs">
          <li
            v-for="(item, index) in selDept"
            :key="index"
            @click="tocoagencyDetail(item.relevanceId)"
          >
            <el-image
              style="cursor: pointer"
              class="hzs_img"
              :src="item.cover ? item.cover : defaultDeptAvatar"
              fit="scale-down"
            >
              <div slot="error" class="image-slot">
                <img class="hzs_img" src="@/assets/images/default_img.jpg" />
              </div>
            </el-image>
          </li>
        </div>
      </div>
    </div>
    <!-- <integralBox class="topLeft"></integralBox> -->
  </div>
</template>
<script>
import integralBox from "@/components/integralBox.vue";
import carousel from "@/components/carousel.vue";
import {
  bannerList,
  goodsList,
  goodBookList,
  getGuessFavouriteNoLogin,
  getGuessFavouriteForLogin,
  homePageselHotBookList,
  selSubjectList,
  selAuthorList,
  selDeptList,
  getConfigKey,
} from "@/api/index/index.js";
import "swiper/css/swiper.css"; //引入swiper样式
import Swiper from "swiper"; //引入swiper
export default {
  components: { integralBox, carousel },
  data() {
    return {
      theme: false, //首页置灰
      defaultImg: require("@/assets/images/default-avatar.jpg"),
      defaultDeptAvatar: require("@/assets/images/inst_default_avatar.png"),
      banner: [], //banner
      goods: [], //新书速递
      goodBook: [], //好书推荐
      GuessFavourite: [], //猜你喜欢
      homePageselHotBook: [], //热门图书
      selSubject: [], //特色专题
      selAuthor: [], //作者
      selDept: [], //合作机构
      goodsType: 0, //热门图书分类
      goodsTypeList: [
        {
          value: "material",
          lable: "教材类",
        },
        {
          value: "reference",
          lable: "教辅类",
        },
        {
          value: "learning",
          lable: "学术类",
        },
        {
          value: "paper",
          lable: "试卷类",
        },
        {
          value: "other",
          lable: "其他类",
        },
        {
          value: "train",
          lable: "培训类",
        },
      ],
      hoverTimer: undefined, //hover延时
      setinter: undefined,
    };
  },
  created() {
    this.getConfig();
    // this.getbannerList();
    this.getgoodsList();
    this.getgoodBookList();
    if (this.$store.getters.token) {
      this.getGuessFavourite(1);
    } else {
      this.getGuessFavourite(0); //0=>未登录   1=>已登录
    }
    this.gethomePageselHotBookList();
    this.getselSubjectList();
    this.getselAuthorList();
    this.getselDeptList();
    clearInterval(this.setinter);
    this.setinter = setInterval(() => {
      this.goodsType++;
      if (this.goodsType === this.goodsTypeList.length) {
        this.goodsType = 0;
      }
      this.gethomePageselHotBookList();
    }, 10000);
  },
  watch: {
    //监听路由变化
    $route(to, from) {
      clearInterval(this.setinter);
      if (to.name === "首页") {
        this.setinter = setInterval(() => {
          this.goodsType++;
          if (this.goodsType === this.goodsTypeList.length) {
            this.goodsType = 0;
          }
          this.gethomePageselHotBookList();
        }, 10000);
      }
    },
  },
  mounted() {},
  methods: {
    //首页置灰
    getConfig() {
      getConfigKey().then((res) => {
        this.UpTheme(res.data);
      });
    },
    UpTheme(data) {
      if (data == 0) {
        this.theme = true;
      } else {
        this.theme = false;
      }
    },
    // banner列表
    getbannerList() {
      bannerList({
        pageNum: 1,
        pageSize: 10,
      }).then((res) => {
        this.banner = res.data;
      });
    },
    //新书推荐
    getgoodsList() {
      goodsList({
        pageNum: 1,
        pageSize: 4,
      }).then((res) => {
        var newdata = JSON.parse(JSON.stringify(res.data));
        newdata.forEach((item, index) => {
          if (item.keyword) {
            newdata[index].keyword = item.keyword.split(",");
          }
        });
        this.goods = newdata;
      });
    },
    //好书排行
    getgoodBookList() {
      goodBookList({
        pageNum: 1,
        pageSize: 10,
      }).then((res) => {
        var that = this;
        this.goodBook = res.data.slice(0, 8);
        setTimeout(() => {
          $(".index_con_box1r_con li").hover(
            function () {
              that.hoverTimer = setTimeout(() => {
                $(this).siblings().find(".icbcr_box_first").hide(100);
                $(this).siblings().find(".icbcr_box").show(100);

                $(this).find(".icbcr_box_first").show(100);
                $(this).find(".icbcr_box").hide(100);
              }, 500);
            },
            function () {
              clearTimeout(that.hoverTimer);
            }
          );
        }, 10);
      });
    },
    //猜你喜欢列表
    getGuessFavourite(tp) {
      //0=>未登录   1=>已登录
      if (tp === 0) {
        getGuessFavouriteNoLogin({
          pageNum: 1,
          pageSize: 10,
        }).then((res) => {
          this.GuessFavourite = res.data;
        });
      } else {
        getGuessFavouriteForLogin({
          pageNum: 1,
          pageSize: 10,
        }).then((res) => {
          this.GuessFavourite = res.data;
        });
      }
    },
    //热门图书
    gethomePageselHotBookList() {
      homePageselHotBookList({
        goodsType: this.goodsTypeList[this.goodsType].value,
      }).then((res) => {
        var newdata = JSON.parse(JSON.stringify(res.data));
        newdata.forEach((item, index) => {
          if (item.keyWord) {
            newdata[index].keyWord = item.keyWord.split(",");
          }
        });
        this.homePageselHotBook = newdata;
      });
    },
    // 查看更多
    viewMore() {
      this.$router.push({
        path: "/bookstore/index",
        query: {
          activeIndex: this.goodsType,
          activeName: this.goodsTypeList[this.goodsType].value,
        },
      });
    },
    bannerdetail(to) {
      //如果轮播图有对应的链接就去跳转
      if (to) {
        window.open(to, "_blank");
      }
    },
    //特色专题
    getselSubjectList() {
      selSubjectList({
        pageNum: 1,
        pageSize: 10,
      }).then((res) => {
        this.selSubject = res.data;
        setTimeout(() => {
          var certifySwiper = new Swiper("#certify .swiper-container", {
            watchSlidesProgress: true,
            slidesPerView: "auto",
            centeredSlides: true,
            loop: false,
            autoplay: true,
            loopedSlides: 5,
            navigation: {
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev",
            },
            pagination: {
              el: ".swiper-pagination",
              //clickable :true,
            },
            on: {
              progress: function (progress) {
                for (var i = 0; i < this.slides.length; i++) {
                  var slide = this.slides.eq(i);
                  var slideProgress = this.slides[i].progress;
                  var modify = 1;
                  if (Math.abs(slideProgress) > 1) {
                    modify = (Math.abs(slideProgress) - 1) * 0.3 + 1;
                  }
                  var translate = slideProgress * modify * 260 + "px";
                  var scale = 1 - Math.abs(slideProgress) / 5;
                  var zIndex = 999 - Math.abs(Math.round(10 * slideProgress));
                  slide.transform(
                    "translateX(" + translate + ") scale(" + scale + ")"
                  );
                  slide.css("zIndex", zIndex);
                  slide.css("opacity", 1);
                  if (Math.abs(slideProgress) > 3) {
                    slide.css("opacity", 0);
                  }
                }
              },
              setTransition: function (transition) {
                for (var i = 0; i < this.slides.length; i++) {
                  var slide = this.slides.eq(i);
                  slide.transition(transition);
                }
              },
            },
          });
        }, 100);
      });
    },
    //作者
    getselAuthorList() {
      selAuthorList().then((res) => {
        // console.log(res)
        this.selAuthor = res.data;
      });
    },
    //合作机构
    getselDeptList() {
      selDeptList().then((res) => {
        this.selDept = res.data;
      });
    },
    todetail(id) {
      let routeUrl = this.$router.resolve({
        path: "/special/specialDetail",
        query: {
          subjectId: id,
        },
      });
      window.open(routeUrl.href, "_blank");
    },
    toauthorDetail(id) {
      let routeUrl = this.$router.resolve({
        path: "/authorLibrary/authorDetail",
        query: {
          authorId: id,
        },
      });
      window.open(routeUrl.href, "_blank");
    },
    tocoagencyDetail(id) {
      let routeUrl = this.$router.resolve({
        path: "/coagency/coagencyDetail",
        query: {
          deptId: id,
        },
      });
      window.open(routeUrl.href, "_blank");
    },
    tobookDetail(id, goodsType) {
      let url = "";
      if (goodsType == "train") {
        url = "/willingLearn/bookstoreDetail";
      } else {
        url = "/bookstore/bookstoreDetail";
      }
      let routeUrl = this.$router.resolve({
        path: url,
        query: {
          goodsId: id,
        },
      });
      window.open(routeUrl.href, "_blank");
    },
  },
};
